.tooltip-container {
  // position: relative;
  display: flex;
  flex-grow: 1;
}

.tooltip-container .tooltip {
  visibility: hidden;
  width: 150px;
  background-color: black;
  color: white;
  font-size: 16px;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  top: 33%;
  left: 10%;
  margin-top: 5px;
  margin-left: -60px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltip-container:hover .tooltip {
  visibility: visible;
  opacity: 1;
}
